.video-popup {
  position: relative;
  //top: 0 !important;
  width: 100%;
  //max-width: 100%;
  height: 100vh;
  //background-color: transparent;
  //overflow: hidden;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  //visibility: hidden;
  display: none;
  &.opened {
    display: flex;
    //visibility: visible;
  }
  .popup-close {
    color: $color-black;
    border-color: $color-black;
    background-color: $color-white;
    &:before,
    &:after {
      background-color: $color-black;
    }
  }
  .popup-content {
    box-shadow: 0 0 30px rgba(0, 0, 0, 0.15);
    //@include pos(absolute, center, center);
    width: 100vw;
    max-width: 960px;
    height: 100vh;
    max-height: 400px !important;
    //overflow: hidden;
    @include media('>=tablet') {
      max-height: 540px !important;  
    }
  }

  iframe {
    //@include pos(absolute, 0, 0);
    width: 100vw;
    max-width: 960px;
    height: 100vh;
    max-height: 540px;
    border: none;
  }

}
